const text1 = `Talabat may collect the following information from users of our site: first name, last name, street address, area and city, phone and contact numbers, e-mail address, GPS location (mobile site) (collectively, "Personally Identifiable Information" or "PII").

In addition, Talabat may collect information regarding Talabat account holders' past Talabat orders, favorite restaurants, customer service inquiries, service/restaurant reviews and certain social networking preferences (e.g. pages you "Like" or "Recommend").

Talabat also uses web analytics software to track and analyze traffic on the site in connection with Talabat advertising and promotion of Talabat services. Talabat may publish these statistics or share them with third parties without including PII.

Non-Personally Identifiable Information (or "Non-PII") is aggregated information, demographic information and any other information that does not reveal your specific identity. We and our third party service providers may collect Non-PII from you, including your MAC address, your computer type, screen resolution, OS version, Internet browser and demographic data, for example your location, gender and date of birth and we may aggregate PII in a manner such that the end-product does not personally identify you or any other user of the Site, for example, by using PII to calculate the percentage of our users who have a particular telephone area code. We and our third party service providers may also use cookies, pixel tags, web beacons, and other similar technologies to better serve you with more tailored information and facilitate your ongoing use of our Site. If you do not want information collected through the use of cookies, there is a simple procedure in most browsers that allows you to decline the use of cookies. To learn more about cookies, please visit http://www.allaboutcookies.org/.

IP Addresses are the Internet Protocol addresses of the computers that you are using. Your IP Address is automatically assigned to the computer that you are using by your Internet Service Provider (ISP). This number is identified and logged automatically in our server log files whenever users visit the Site, along with the time(s) of such visit(s) and the page(s) that were visited. Collecting IP Addresses is standard practice on the Internet and is done automatically by many websites.
`;

const header1= 'Personal Contact Information'



const header2 = 'Use of your information'

const text2 = `Talabat uses PII to create users' Talabat accounts, to communicate with users about Talabat services, to offer users additional services, promotions and special offers and to charge for purchases made through Talabat. Users may affirmatively opt-out of receiving promotional communications from Talabat by visiting www.talabat.com. Personal account page and providing Talabat with their e-mail address via the opt-out link. Talabat may also use PII to enforce Talabat terms of use and service.

Talabat uses cookies to remember users on the site and to enhance users' experience on the site. For example, when users with Talabat accounts return to the site, cookies identify those users and allow the site to provide certain user-specific information such as Talabat login information, choice of website language and country selection in addition to any other information we might collect in the future. Talabat does not sell the information it collects to third parties. Talabat shares collected PII to third-party vendors and service providers with whom Talabat works to provide application programming interfaces ("APIs") and other functions for the site in connection with the delivery of Talabat services. In addition, Talabat shares users' Talabat order content, special order instructions, first and last name, street address and telephone number with restaurants where users' orders are placed to the extent necessary to process those orders. Talabat may also disclose PII to third parties such as attorneys, collection agencies, tribunals or law enforcement authorities pursuant to valid requests in connection with alleged violations of Talabat terms of use and service or other alleged contract violations, infringement or similar harm to persons or property.

User generated content posted through the site such as service/restaurant reviews and certain social networking preferences (e.g. pages you "Like" or "Recommend") may be viewed by the general public. Accordingly, Talabat cannot ensure the privacy of any PII included in such user generated content.

Non-Personally Identifiable Information. Because Non-PII does not personally identify you, we may use such information for any purpose. In addition, we reserve the right to share such Non PII, which does not personally identify you, with our affiliates and with other third parties, for any purpose. In some instances, we may combine Non-PII with PII (such as combining your name with your geographical location). If we do combine any Non-PII with PII, the combined information will be treated by us as PII hereunder as long as it is so combined.

IP Addresses. We use IP addresses for purposes such as calculating site usage levels, helping diagnose server problems, and administering the site. We may also use and disclose IP Addresses for all the purposes for which we use and disclose PII. Please note that we treat IP Addresses, server log files and related information as Non-PII, except where we are required to do otherwise under applicable law.`

const header3 = `Your Email Address`


const text3 = `To register for an account, we require you to supply us with your email address, or other information needed to contact you online and over the phone. We use your e-mail address and other contact information you provide us with to confirm your order and to communicate with you in case of an order problem, so one cannot register without an e-mail address. If you supply us with your email address, you may later access it, update it, modify it and delete any inaccuracies by accessing your account through my account information link on the user main page. You may also choose simply not to provide us with your email address; however you will not be able to register on the website and place orders. We communicate part of your contact information (name, surname, order delivery address and phone number) with the member restaurant from which you have placed an order. We also use this information to facilitate and improve your use of the website, to communicate with you, for internal purposes and to comply with any requirements of law. This information may be disclosed to our staff and to third parties involved in the delivery of your order or the analysis and support of your use of the website. We do not sell and will not divulge your personal contact information to third parties other than as specified in this Privacy Policy without your permission unless we are legally entitled or obliged to do so (for example, if required to do so by Court order or for the purposes of prevention of fraud or other crime).`


const header4 = `ATM, Credit Cards and Payment Information`
const text4  = `When you place an order through the website, you are required to select a method of payment. Talabat is not interested in your debit/credit card information nor do store any of your debit/credit information, since orders could be paid at the door directly to the restaurant through methods of payment such as cash, credit card or payment checks. Online debit/ credit card payment is also an option and for some restaurants it can be the only method of payment. For transactions with online debit/credit cards, we transmit your entire card information to the appropriate debit/credit card company in an encrypted format with globally accepted rules and applications during order processing. Upon your choice, we keep a part of your card information in an encrypted format, taking precaution to maintain physical, electronic and procedural safeguards over your credit card information.`
const header5 = `Order Placement Information`
const text5 = `Personal information required during the order process includes name, address, phone number, email address and other similar information used to identify you and complete an order. Personal information collected at our site will be shared with a restaurant when processing your order. We have an agreement with all member restaurants, restricting disclosure or further processing of personal information provided to them by us. Your personal information will be used to notify you of your order status. Personal information will not be shared with any of our business partners or affiliates without your permission. It is treated as confidential, and will not be disclosed to outside parties, unless compelled by applicable legislation. We only send marketing emails to those people who have specifically requested to receive this information.`
