const header2= `Description of the Services`
const text2 = `Talabat.com allows its members who ratify the agreement to order from shops online.
Talabat.com provides a way for you to communicate your orders to delivery shops displayed on this website.
The purpose of this site is to provide a simple and convenient service to consumers, linking them to shops in their area that offer takeaway food. Interactive menus allow customers to build and submit orders at the click of a button.
We have created the website to serve as a food court online market. Talabat.com is a commercial service for food delivery online.
Talabat.com does not sell or interfere in any way in the production of any food produced at the shops. But what this online market offers to the users is only providing the ability to search for and find local shops that deliver or make food available to their addresses and enables ordering this food through this website. The shops are obligated to comply with country local laws, rules, regulations, and standards pertaining to food preparation, sale, marketing and safety.

However, it is important for users to understand that Talabat.com does not in any way independently verify the credentials, representations or products of shops, the ingredients or the quality of any products, or that a shop is in compliance with applicable laws. Buyers must make themselves comfortable through information provided by shops on the website or as requested by buyers directly from the shops as to the quality and reliability of the shops, as well as to their compliance with applicable laws.
Talabat.com does not in any way guaranty the quality of any food or that any food complies with applicable laws and does not guarantee matching the menu displayed on the site of what is actually provided to the users.
In addition, a shop may represent that food preparation is in accordance with special standards such as "organic," "macrobiotic" or allergen-specific standards such as "nut-free", "gluten-free", or "lactose-free". However, Talabat.com does not independently investigate or verify such representations.
Talabat.com shall not be liable or responsible for any food or services offered by shops that is unhealthy, is the cause of injury, that is otherwise unacceptable to buyers or that does not meet the expectation of buyers in any manner.
Buyers are solely responsible for verifying the accuracy of delivery addresses, and Talabat.com shall have no liability or responsibility for any such erroneous addresses. All food preparation and deliveries are the sole responsibility of the shop accepting the order. Please be aware that the shop will do everything they can to meet the delivery time specified, however delivery times may be affected by factors beyond their control and therefore cannot be guaranteed. We will inform you if we become aware of an unexpected delay.`
const header3= `How to make an order and how it is processed`
const text3 = `Once you have selected your order from the menu of your chosen Delivery shop you will be given the opportunity to submit your order by clicking on Checkout button. Please note it is important that you check the information that you enter and correct any errors before clicking on the Checkout button since once you click on this input errors cannot be corrected. On receipt of your order, Talabat.com will begin processing your order and we will send a message on the final page that your order has been received and sent successfully to the shop. Where any payment you make is not authorized you will be returned to the previous page on the website and we shall not be obliged to provide the services.`
const header4= `Order Cancellation`
const text4 = `You have the right to cancel an order up to 5 minutes from placing the order on our website.
While every effort is made to ensure that accurate pricing and descriptions are maintained, we reserve the right to cancel any order that is based on inaccurate information.

An order may be subsequently cancelled by a shop after you have received a confirmation that it has been sent to the shop. Talabat.com and our partner shops reserve the right to cancel any order, before or after acceptance, and will notify you immediately of any such cancellation.`
